<template>
  <div>
    <self-table :tableData="tableData" :tableLabel="tableLabel" @delete="handleDelete" @edit="handleEdit"></self-table>
  </div>
</template>

<script>
import selfTable from 'views/table/selfTable.vue'
export default {
  components: {
    selfTable
  },
  data() {
    return {
      tableData: [],
      tableLabel: {
        name: '广告名',
        _id: 'id',
        date: '创建时间'
      }
    }
  },
  mounted() {
    this.fetch()
  },
  methods: {
    async fetch() {
      const res = await this.$http.get('/crud/ads/list')
      // console.log(res)
      // res.data.data.map(item => {
      //   if (item.parent) {
      //     item.parent = item.parent.name
      //   }
      // console.log(item)
      // return item
      // })
      const arr = res.data.data.map(item => {
        item.date = this.$dayjs(item.date).format('YYYY/MM/DD')
        return item
      })
      this.tableData = arr
      // console.log(res.data)
    },
    handleEdit(row) {
      const { _id } = row
      this.$router.push(`/ads/edit/${_id}`)
    },
    handleDelete(row) {
      const { name, _id } = row
      this.$confirm(`是否删除${name}广告?`, '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await this.$http.delete(`/crud/ads/${_id}`)
          this.$message({
            type: 'success',
            message: '删除成功!'
          })
          this.fetch()
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: '已取消删除'
          })
        })
    }
  }
}
</script>

